'use client';

import React, { useState, useTransition, useRef } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { 
  Sparkles, 
  Brain, 
  Wand2, 
  ImageIcon, 
  Mic, 
  FileEdit, 
  CheckSquare, 
  Lightbulb, 
  Paperclip,
  Save,
  PlusCircle,
  X
} from 'lucide-react';
import toast from 'react-hot-toast';

// --- UI & TYPE IMPORTS ---
import Card from '@/components/ui/Card';
import Label from '@/components/ui/Label';
import Textarea from '@/components/ui/Textarea';
import { Button } from '@/components/ui/Button';
import AnswerInput from '@/components/ui/AnswerInput';
import FileUpload from '@/components/ui/FileUpload';
import CreateQuestionSidebar from '@/components/questions/CreateQuestionSidebar';
import { Difficulty, Topic, Question, QuestionFormat, SubQuestion } from '@/lib/definitions'; // Assuming full Question type is defined

// --- SERVER ACTIONS ---
// We will define the actions needed and then create them.
import { generateQuestionWithAI, createQuestionAction } from './actions';

const CreateQuestionPage: React.FC = () => {
  const router = useRouter();
  const [isExtracting, startExtractTransition] = useTransition();
  const [isSaving, startSaveTransition] = useTransition();

  // State for the AI input
  const [rawInput, setRawInput] = useState('');
  
  // This state will hold the full, structured question data after AI generation or manual input
  const [questionData, setQuestionData] = useState<Partial<Question>>({
    category: 'TOEIC', // Default
    part: 5, // Default
    topic: Topic.GRAMMAR,
    format: QuestionFormat.MULTIPLE_CHOICE,
    subQuestions: [{
      content: '',
      options: ['', '', '', ''],
      correctAnswer: [],
      explanation: '',
      tags: [],
    }],
    metadata: {
        level: Difficulty.MEDIUM
    }
  });

  // Helper for easy access to the first sub-question
  const subQuestion = questionData.subQuestions?.[0];

  const handleAiExtract = () => {
    if (!rawInput.trim()) {
        toast.error("Vui lòng nhập nội dung câu hỏi thô.");
        return;
    }

    startExtractTransition(async () => {
      const result = await generateQuestionWithAI(rawInput);

      if (result.success && result.data) {
        toast.success('AI đã phân tích câu hỏi thành công!');
        const aiData = result.data as Question;

        // Find the correct index from the answer text
        const correctIdx = aiData.subQuestions[0].options.indexOf(aiData.subQuestions[0].correctAnswer[0]);

        // Update the main state with the AI-generated data
        setQuestionData(aiData);

      } else {
        toast.error(result.error || 'AI không thể xử lý yêu cầu này.');
      }
    });
  };

  // Function to update the main question data state
  const updateQuestionField = (field: keyof SubQuestion, value: any) => {
    setQuestionData(prev => ({
        ...prev,
        subQuestions: [{
            ...(prev.subQuestions?.[0] || {}),
            [field]: value
        }]
    }));
  };

  const updateOption = (idx: number, val: string) => {
    const newOptions = [...(subQuestion?.options || [])];
    newOptions[idx] = val;
    updateQuestionField('options', newOptions);
  };

  const setCorrectAnswer = (optionText: string) => {
    updateQuestionField('correctAnswer', [optionText]);
  }

  // --- HANDLER CHO VIỆC LƯU TRỮ ---
  const handleSave = async (andContinue = false) => {
    // Basic validation
    if (!subQuestion?.content?.trim()) {
        return toast.error("Nội dung câu hỏi không được để trống.");
    }

    startSaveTransition(async () => {
        const result = await createQuestionAction(questionData as Question);
        if (result.success) {
            if (andContinue) {
                toast.success('Đã lưu! Sẵn sàng cho câu hỏi tiếp theo.');
                // Reset state
                setRawInput('');
                setQuestionData({
                    category: 'TOEIC', part: 5, topic: Topic.GRAMMAR, format: QuestionFormat.MULTIPLE_CHOICE,
                    subQuestions: [{ content: '', options: ['', '', '', ''], correctAnswer: [], explanation: '', tags: [] }],
                    metadata: { level: Difficulty.MEDIUM }
                });
            } else {
                toast.success('Câu hỏi đã được lưu thành công!');
                router.push('/dashboard/teacher/questions');
            }
        } else {
            toast.error(result.error || 'Không thể lưu câu hỏi.');
        }
    });
  }

  return (
    <div className="max-w-7xl mx-auto w-full px-4 md:px-6 py-8">
      <div className="mb-8">
        {/* Breadcrumbs & Title */}
      </div>

      {/* AI Input Section */}
      <Card as="section" className="mb-8 !p-8 shadow-xl border-2 border-[#2b8cee]/30 relative overflow-hidden">
         <div className="absolute top-0 right-0 p-4 opacity-10 pointer-events-none"><Sparkles size={160} className="text-[#2b8cee]" /></div>
         <div className="relative z-10 flex flex-col gap-6">
           <div className="flex items-center gap-4">
             <div className="p-3 rounded-lg bg-gradient-to-br from-[#2b8cee] to-blue-400 text-white shadow-md"><Brain size={28} /></div>
             <div>
               <h2 className="text-xl font-bold text-gray-900">Tạo nhanh với AI</h2>
               <p className="text-sm text-gray-500">Dán nội dung từ Word, PDF hoặc Website vào đây.</p>
             </div>
           </div>
          
           <Textarea 
             value={rawInput}
             onChange={(e) => setRawInput(e.target.value)}
             className="min-h-[180px] !p-6 text-base placeholder:text-gray-400"
             placeholder="Ví dụ: The new regulations will be _______ to all employees starting next month. A) applied B) apply C) applying D) application"
           />

           <div className="flex flex-wrap items-center justify-between gap-4">
             <div className="flex items-center gap-2 text-sm text-gray-500">
               <Paperclip size={18} />
               <span>Đính kèm hình ảnh/âm thanh bên dưới nếu cần</span>
             </div>
             <Button 
               onClick={handleAiExtract}
               disabled={isExtracting || !rawInput.trim()}
               className="bg-gradient-to-br from-[#2b8cee] to-blue-400 ai-glow hover:scale-[1.02] active:scale-[0.98] text-white px-8 py-4 rounded-xl font-bold text-base flex items-center gap-3 transition-all shadow-lg disabled:opacity-70 disabled:cursor-not-allowed"
             >
               <Wand2 size={24} className={isExtracting ? 'animate-spin' : 'animate-pulse'} />
               {isExtracting ? 'Đang phân tích...' : 'Phân tích với AI'}
             </Button>
           </div>
         </div>
      </Card>

      {/* Main Layout: Content + Sidebar */}
      <div className="grid grid-cols-1 lg:grid-cols-10 gap-8 items-start">
        {/* Main Form */}
        <main className="lg:col-span-7 flex flex-col gap-8">
          <Card>
            <h3 className="text-sm font-bold uppercase tracking-wider text-gray-500 mb-4">Tài liệu đính kèm</h3>
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
              <FileUpload icon={<ImageIcon size={24} />} name="imageFile" accept="image/*" />
              <FileUpload icon={<Mic size={24} />} name="audioFile" accept="audio/*" />
            </div>
          </Card>

          <Card>
            <Label icon={<FileEdit size={22} className="text-[#2b8cee]" />}>Nội dung câu hỏi chính xác</Label>
            <Textarea 
              value={subQuestion?.content || ''}
              onChange={(e) => updateQuestionField('content', e.target.value)}
              className="min-h-[120px]"
              placeholder="AI sẽ tự động điền vào đây sau khi trích xuất..."
            />
          </Card>

          <Card>
            <div className="flex justify-between items-center mb-4">
              <Label icon={<CheckSquare size={22} className="text-[#2b8cee]" />}>Danh sách đáp án</Label>
              <span className="text-xs font-medium text-gray-500 bg-gray-100 px-3 py-1 rounded-full">Chọn vào ô để xác định đáp án đúng</span>
            </div>
            <div className="flex flex-col gap-4">
              {subQuestion?.options.map((opt, i) => (
                <AnswerInput
                  key={i}
                  index={i}
                  option={opt}
                  isChecked={subQuestion.correctAnswer?.[0] === opt}
                  onCheck={() => setCorrectAnswer(opt)}
                  onUpdate={(value) => updateOption(i, value)}
                />
              ))}
            </div>
          </Card>

          <Card>
            <Label icon={<Lightbulb size={22} className="text-[#2b8cee]" />}>Lời giải thích của AI</Label>
            <Textarea 
              value={subQuestion?.explanation || ''}
              onChange={(e) => updateQuestionField('explanation', e.target.value)}
              className="min-h-[140px]"
              placeholder="Giải thích chi tiết về ngữ pháp/từ vựng của câu hỏi..."
            />
          </Card>
        </main>

        {/* Sidebar */}
        <aside className="lg:col-span-3">
           <CreateQuestionSidebar 
             difficulty={questionData.metadata?.level || Difficulty.MEDIUM}
             setDifficulty={(val) => setQuestionData(prev => ({...prev, metadata: {...prev.metadata, level: val}}))}
             topic={questionData.topic || Topic.GRAMMAR}
             setTopic={(val) => setQuestionData(prev => ({...prev, topic: val}))}
             tags={subQuestion?.tags || []}
             setTags={(val) => updateQuestionField('tags', val)}
             onSave={() => handleSave(false)}
             onSaveAndContinue={() => handleSave(true)}
             onCancel={() => router.push('/dashboard/teacher/questions')}
             isSaving={isSaving}
           />
        </aside>
      </div>
    </div>
  );
};

export default CreateQuestionPage;
