import { ObjectId } from 'mongodb';

// =======================================================================
// ĐỊNH NGHĨA CHUNG (CHO LAYOUT)
// =======================================================================

export interface NavLink {
  name: string;
  href: string;
}

export interface FooterLink {
  name: string;
  href: string;
}

export interface FooterLinkColumn {
  title: string;
  links: FooterLink[];
}

export interface SocialLink {
  platform: 'facebook' | 'instagram' | 'twitter';
  href: string;
}

export interface FooterContent {
  _id?: ObjectId;
  brandName: string;
  description: string;
  socialLinks: SocialLink[];
  linkColumns: FooterLinkColumn[];
  policyLinks: FooterLink[];
  copyright: string;
}

// =======================================================================
// ĐỊNH NGHĨA CHO TRANG QUẢN LÝ CÂU HỎI
// =======================================================================

export enum Difficulty {
  EASY = 'Dễ',
  MEDIUM = 'Trung bình',
  HARD = 'Khó',
}

// --- SỬA LỖI: THÊM ENUM TOPIC ---
// Thêm enum cho Topic để khắc phục lỗi import.
// Việc quản lý tập trung các chủ đề giúp đảm bảo tính nhất quán và dễ mở rộng.
export enum Topic {
  GRAMMAR = 'Ngữ pháp',
  VOCABULARY = 'Từ vựng',
  READING = 'Đọc hiểu',
  LISTENING = 'Nghe hiểu',
  WRITING = 'Viết',
  GENERAL = 'Chủ đề chung',
}
// --- KẾT THÚC SỬA LỖI ---

export interface Question {
  id: string;
  content: string;
  topic: string;
  difficulty: Difficulty;
  createdAt: string;
  imageUrl?: string;
  type: 'text' | 'image' | 'audio';
}
